import React, { Component } from 'react';
import './style.css';
import ResultComponent from './components/Result';
import KeyPadComponent from "./components/KeyPad";

class App extends Component {
    constructor(){
        super();
        this.state = {result: "" }
    }

    onClick = button => {
        if(button === "="){ this.calculate() }
        else if(button === "C"){ this.reset() }
        else if(button === "CE"){ this.backspace() }
        else { this.setState({result: this.state.result + button })
        }
    };

    calculate = () => {
        var checkResult = ''
        if(this.state.result.includes('--')){
            checkResult = this.state.result.replace('--','+')
        } else {checkResult = this.state.result}

        try {
            this.setState({result: (eval(checkResult) || "" ) + "" })
        } catch (e) {
            this.setState({ result: "error"})
        }
    };

    reset = () => {this.setState({result: ""})};

    backspace = () => {this.setState({result: this.state.result.slice(0, -1)})};

    render() {
        return (
            <div>
                <div className="calculator-body">
                    <h1>Calculator</h1>
                    <p>Type in your equation here</p>
                    <p>To restart, press CE or C</p>
                    <ResultComponent result={this.state.result}/>
                    <KeyPadComponent onClick={this.onClick}/>
                </div>
            </div>
        );
    }
}
export default App;